import { Bread } from 'Bread'
import React, { PureComponent } from 'react'
import style from './components/style/Style.scss'
import Tool from './components/./Tool'
import OperationModal from './components/./OperationModal'
import { connect } from 'react-redux'
import { mapDispatchToProps } from './redux/mapDispatchToProps'
import { API_DIFFAPPORTIONMENTS_COMPANY_LIST } from 'api'
import { getUrlDataObj, countTableScroll } from 'util'
import { Form, Button, Row, Col, Table, DatePicker, Tooltip } from 'antd'
import { CsxFormSelect } from 'Form'
const FormItem = Form.Item
import moment from 'moment'
@connect(
  state => ({
    ...state.diffApportionmentDet,
    menuPermissionIds: state.menuPermissionIds
  }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      editOrCreate: '', //  create -- 新建      edit -- 详情、 编辑
      flag_apportionmentInTable: '', // 差异分摊 : allowApportionment, forbiddenApportionment
      flag_postTable: '', // 差异过账 : allowPost, forbiddenPost
      flag_checkData: '', // 查看数据 : allowCheckData, forbiddenCheckData
      stepId: '',
      hasGetSteps: false //  是否已经获取步骤
    }
    // update({tableDataSource: mockData})
  }
  componentDidMount() {
    const { location = {}, match = {}, handleGetListDet, update } = this.props
    const { search = '' } = location
    const { params = '' } = match
    const { id = '' } = params
    const {
      editOrCreate = '',
      flag_apportionmentInTable = '',
      flag_postTable = '',
      flag_checkData = '',
      statusCode = '',
      company = '',
      period = '',
      companyName = ''
    } = getUrlDataObj(search || '')
    if (id && editOrCreate == 'edit') {
      // 点击查看详情
      handleGetListDet({ searchParams: { id } }, () => {
        let companyObj = {
          value: decodeURIComponent(company),
          children: decodeURIComponent(`${company} ${companyName}`)
        }
        update({
          searchParams: {
            companyCode: company,
            companyName,
            company: companyObj,
            period: moment(period).format('YYYY-MM'),
            initDate: moment(period)
          }
        })
      })
    } else {
      // 点击新增 初始化日期为上一个月
      const prevMonth = moment().subtract(1, 'M')
      update({
        searchParams: {
          initDate: prevMonth,
          period: prevMonth.format('YYYY-MM')
        }
      })
    }
    this.setState({
      editOrCreate,
      flag_apportionmentInTable: flag_apportionmentInTable === 'allowApportionment' ? true : false,
      flag_postTable: flag_postTable === 'allowPost' ? true : false,
      flag_checkData: flag_checkData === 'allowCheckData' ? true : false,
      statusCode,
      stepId: id
    })
  }
  componentWillUnmount() {
    const { update } = this.props
    update({ searchResult: [], searchParams: {} })
  }
  handleSelectChange = () => {}
  // 查询数据
  query = () => {
    const { handleGetList, searchParams, handleGetListDet } = this.props
    const { editOrCreate, stepId } = this.state
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        let reqObj = { ...searchParams }
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            reqObj[i] = reqObj[i].value
          }
        }
        delete reqObj.initDate
        if (editOrCreate == 'edit') {
          handleGetListDet({ searchParams: { id: String(stepId) } }, data => {
          })
        } else {
          let dataObj = {
            companyCode: reqObj.companyCode,
            companyName: reqObj.companyName,
            shareDate: reqObj.period
          }
          handleGetList({ searchParams: { ...dataObj, page: 1 } }, () => {
            this.setState({
              hasGetSteps: true
            })
          })
        }
      }
    })
  }
  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    update({ searchParams: { ...searchParams, ...values } })
  }
  renderFormItem = () => {
    const {
      searchParams = {},
      form: { getFieldDecorator }
    } = this.props
    const { initDate, company = '', companyCode } = searchParams
    const { editOrCreate } = this.state
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 10 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 14 }
      }
    }
    return (
      <div className={style.form_item_det}>
        <Row type='flex' justify='start'>
          <Col span={8} style={{ height: '44px' }}>
            {/* <div className='commonInput'> */}
            <FormItem {...formItemLayout} label='公司' hasFeedback>
              {getFieldDecorator('company', {
                initialValue: company,
                rules: [
                  {
                    required: true,
                    message: '请填写!'
                  }
                ]
              })(
                <CsxFormSelect
                  disabled={editOrCreate == 'edit' ? true : false}
                  allowClear={true}
                  dateApi={API_DIFFAPPORTIONMENTS_COMPANY_LIST}
                  selectItem={{
                    data: companyCode,
                    key: 'code',
                    valueKey: ['code', 'name']
                  }}
                  handleSelectChange={data => {
                    const { value = '', children = '' } = data
                    console.log('data is', data)
                    this.onInputChange({
                      companyCode: value,
                      companyName: children && children.length > 0 && children.split(' ')[1],
                      company: `${children}`
                    })
                  }}
                />
              )}
            </FormItem>
            {/* </div> */}
          </Col>
          <Col span={8} style={{ height: '44px' }}>
            <div className='commonInput'>
              <div className={style.dataPickerDet}>
                <label>期间：</label>
                <div className='box' style={{ marginRight: 0 }}>
                  <DatePicker
                    disabled={true}
                    onChange={time => {
                      let period = time && `${moment(time).format('YYYY-MM')}`
                      this.onInputChange({
                        period,
                        initDate: time
                      })
                    }}
                    format={'YYYY-MM'}
                    value={initDate}
                  />
                </div>
              </div>
            </div>
          </Col>

          <Col span={8} style={{ height: '44px' }}>
            {editOrCreate == 'create' ?
              <Row type='flex' justify='end'>
                <Button
                  type='primary'
                  className={style.mr10}
                  onClick={this.query}
                >
                  获取步骤
                </Button>
              </Row>
            : editOrCreate == 'edit' ?
              <Row type='flex' justify='end'>
                <Button
                  type='primary'
                  className={style.mr10}
                  onClick={this.query}
                >
                  刷新
                </Button>
              </Row>
             : null}
          </Col>
        </Row>
      </div>
    )
  }
  renderFormItemNext = () => (
    <div className={style.form_item_det}>{this.renderFormItemInput()}</div>
  )
  renderFormItemInput = () => {
    const {
      flag_apportionmentInTable,
      flag_postTable,
      flag_checkData
    } = this.state
    return (

      <Row type='flex' justify='start'>
        <Col span={24}>
          <Tool
            history={this.props.history}
            location={this.props.location}
            flag_apportionmentInTable={flag_apportionmentInTable}
            flag_postTable={flag_postTable}
            flag_checkData={flag_checkData}
            editOrCreate={this.state.editOrCreate}
            hasGetSteps={this.state.hasGetSteps}
          />
        </Col>
      </Row>
    )
  }
  // 查看消息详情
  doCheckDet = () => {
    const { update } = this.props
    update({ isShowModal: true, modalTitle: '生产波次维护', modalData: '' })
  }
  renderFormItemTable = () => {
    const { tableLoading, searchResult = [] } = this.props
    const columns = [
      {
        title: '期间',
        width: 100,
        dataIndex: 'period',
        key: 'period'
      },
      {
        title: '公司代码',
        width: 100,
        dataIndex: 'company',
        key: 'company'
      },
      {
        title: '步骤',
        width: 100,
        dataIndex: 'step',
        key: 'step'
      },
      {
        title: '步骤值',
        width: 100,
        dataIndex: 'stepValue',
        key: 'stepValue'
      },
      {
        title: '步骤描述',
        width: 200,
        dataIndex: 'stepDesc',
        key: 'stepDesc'
      },
      {
        title: '处理状态',
        width: 100,
        dataIndex: 'statusDesc',
        key: 'statusDesc',
        render: text => text
      },
      {
        title: '消息文本',
        width: 200,
        dataIndex: 'message',
        key: 'message',
        render: text => (
          <div className={style.textInTable}>
            <Tooltip
              placement='top'
              title={text}
              trigger='hover'
              overlayClassName={'tootip_in_table'}
            >
              <span className={style.textLeft}>{text}</span>
            </Tooltip>
          </div>
        )
      }
    ]
    return (
      <Row>
        <div className='table-group'>
          <Table
            rowKey={record => record.step}
            loading={tableLoading}
            columns={columns}
            dataSource={searchResult}
            scroll={{ x: countTableScroll(columns) }}
            // onChange={this.onChange}
            pagination={false}
          />
        </div>
      </Row>
    )
  }
  render() {
    const { editOrCreate = '' } = this.state
    let msg =
      editOrCreate == 'edit'
        ? '查看差异分摊'
        : editOrCreate == 'create'
        ? '获取差异分摊'
        : ''
    return (
      <section>
        <Bread route={['成本中心', '月底差异分摊', msg]} />
        <Form autoComplete='off'>
          {this.renderFormItem()}
          {this.renderFormItemNext()}
        </Form>
        {this.renderFormItemTable()}
        {this.props.isShowModal ? <OperationModal /> : null}
      </section>
    )
  }
}
export default Form.create()(App)



