import React, { PureComponent } from 'react'
import { Modal, Row, Col, Input, Form, Button, message } from 'antd'
import { CsxFormSelect, CsxMultipleSearch } from 'Form'
import {
  API_GET_LOCATION_CODEANDNAME,
  API_PRODUCTIONFREQUENCYS_BATCHTYPE_LIST,
  API_PRODUCTIONFREQUENCYS_SOURCE_NAME_LIST,
  API_GET_LOCATION_ORDER_CODEANDNAME
} from 'api'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
const FormItem = Form.Item
import { getData } from 'util'
@connect(
  state => ({ ...state.productionFrequency, userLoation: state.userLoation }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      selectType: '', // 用户选的生产批次的类型
      batchList: '' // 波次列表
    }
  }
  componentDidMount() {
    // 数据初始化
    const { modalData, getSingleDet } = this.props
    if (modalData) {
      //编辑
      const { id = '' } = modalData
      const params = {
        urlParams: [`${id}`]
      }
      getSingleDet(params, data => {})
    }
    getData({ api: API_PRODUCTIONFREQUENCYS_BATCHTYPE_LIST }).then(data => {
      if (data.result) {
        this.setState({ batchList: data.result })
      }
    })
  }
  handleSubmit = e => {
    const { form, modalData, handleAddOrEdit } = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        console.log('form values is', values)
        let reqObj = { ...values }
        let productTimeArr = []
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            if (i == 'batch') {
              reqObj.batch = reqObj[i].value
            }
            if (i == 'location') {
              reqObj.location = reqObj[i].children
            }
            if (i == 'typeValueSourceLocation' || i == 'typeValueSource') {
              reqObj.typeValue = reqObj[i].value
            }
            if (i == 'typeKey') {
              reqObj.typeKey = reqObj[i].value
            }
          } else {
            if (i == 'startTime') {
              productTimeArr.unshift(reqObj[i])
            }
            if (i == 'endTime') {
              productTimeArr.push(reqObj[i])
            }
          }
        }
        reqObj.productTime = productTimeArr.join('-')
        let result = {
            data: reqObj
          },
          msg = '创建成功'
        // 判断是否是修改,加ID
        if (modalData && modalData.id) {
          result.data.id = modalData.id
          // result.urlParams = [modalData.id]
          msg = '编辑成功'
        }
        handleAddOrEdit({ ...result }, () => {
          message.success(msg)
          const { handleGetList, searchParams, page, size } = this.props
          handleGetList({ searchParams: { page, size, ...searchParams } })
        })
      }
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const { update } = this.props
    update({ isShowModal: false })
  }
  // 改变当前的输入值
  onInputChange(values) {
    const { update, modalData } = this.props
    update({ modalData: { ...modalData, ...values } })
  }
  handleSelectType = data => {
    const { update, modalData = {} } = this.props
    const { typeKey = {} } = modalData
    const { value = '' } = typeKey
    if (value == (data && data.value)) {
      // 类型不变的时候
      return false
    }
    // 判断是否是修改
    // if (modalData && modalData.id) {
    //   update({ modalData: { ...modalData, typeKey: data } })
    // } else {
    //   update({ modalData: { ...modalData, typeKey: data } })
    // }
    update({ modalData: { ...modalData, typeKey: data } })
  }
  render() {
    const {
      modalTitle,
      form: { getFieldDecorator },
      modalTuttonLoading,
      modalData
    } = this.props
    const { batchList = [] } = this.state
    const { typeKey = {} } = modalData
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 }
      }
    }
    const typeOptions = [
      { key: '1', value: '渠道' },
      { key: '2', value: '地点' }
    ]
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        footer={null}
        width='500px'
      >
        <Form onSubmit={this.handleSubmit} autoComplete='off'>
          <Row gutter={16}>
            <Col span={20}>
              <FormItem {...formItemLayout} label='工厂编码' hasFeedback>
                {getFieldDecorator('location', {
                  initialValue: modalData.location,
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(
                  <CsxFormSelect
                    dateApi={API_GET_LOCATION_CODEANDNAME}
                    label='工厂编码/名称'
                    selectItem={{
                      key: 'codeAndName',
                      valueKey: ['code', 'name']
                    }}
                  />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={20}>
              <FormItem {...formItemLayout} label='类型' hasFeedback>
                {getFieldDecorator('typeKey', {
                  initialValue: modalData.typeKey,
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(
                  <CsxFormSelect
                    data={typeOptions}
                    selectItem={{ key: 'key', valueKey: 'value' }}
                    handleSelectChange={this.handleSelectType}
                  />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            {typeKey && typeKey.value == '1' ? (
              <Col span={20}>
                <FormItem {...formItemLayout} label='渠道名称' hasFeedback>
                  {getFieldDecorator('typeValueSource', {
                    initialValue: modalData.typeValueSource,
                    rules: [
                      {
                        required: true,
                        message: '请填写!'
                      }
                    ]
                  })(
                    <CsxFormSelect
                      allowClear={true}
                      dateApi={API_PRODUCTIONFREQUENCYS_SOURCE_NAME_LIST}
                      selectItem={{ key: 'codeAndName', valueKey: ['name'] }}
                    />
                  )}
                </FormItem>
              </Col>
            ) : typeKey && typeKey.value == '2' ? (
              <Col span={20} className={'multiple_search_modal'}>
                <FormItem {...formItemLayout} label='地点编码'>
                  {getFieldDecorator('typeValueSourceLocation', {
                    initialValue: modalData.typeValueSourceLocation,
                    rules: [
                      {
                        required: true,
                        message: '请填写!'
                      }
                    ]
                  })(
                    <CsxMultipleSearch
                      dateApi={API_GET_LOCATION_ORDER_CODEANDNAME}
                      searchParamsKey='location'
                      dropdownClassName={'multipleSearchSelect'}
                      selectItem={{
                        key: 'codeAndName',
                        valueKey: ['code', 'name']
                      }}
                      onChangeSelect={data => {
                        let { value } = data
                        value = value && value.length > 0 && value.join(',')
                        this.onInputChange({
                          typeValueSourceLocation: { ...data, value }
                        })
                      }}
                    />
                  )}
                </FormItem>
              </Col>
            ) : null}
          </Row>
          <Row gutter={16}>
            <Col span={20}>
              <FormItem {...formItemLayout} label='生产波次' hasFeedback>
                {getFieldDecorator('batch', {
                  initialValue: modalData.batch,
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(
                  <CsxFormSelect
                    allowClear={true}
                    dateApi={API_PRODUCTIONFREQUENCYS_BATCHTYPE_LIST}
                    selectItem={{ key: 'codeAndName', valueKey: ['name'] }}
                    handleSelectChange={data => {
                      const { update } = this.props
                      const { value = '' } = data
                      let dataSelected =
                        batchList &&
                        batchList.length > 0 &&
                        batchList.filter((item, index, arr) => {
                          const { code = '' } = item
                          let batchKey = value.split(' ')[0]
                          return code == batchKey
                        })
                      const { startTime = '', endTime = '' } =
                        dataSelected && dataSelected[0]
                      update({
                        modalData: {
                          ...modalData,
                          startTime,
                          endTime
                        }
                      })
                    }}
                  />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row>
            <Col span={20}>
              <div className={'doubleInputModal'}>
                <FormItem
                  label='生产时间'
                  labelCol={{
                    xs: { span: 24 },
                    sm: { span: 8 }
                  }}
                  wrapperCol={{
                    xs: { span: 24 },
                    sm: { span: 7 }
                  }}
                >
                  {getFieldDecorator('startTime', {
                    initialValue: modalData.startTime,
                    rules: [
                      {
                        required: true,
                        message: '不能为空!'
                      }
                    ]
                  })(<Input disabled />)}
                </FormItem>
                <span className={'doubleInputModalDivider'}>～</span>
                <div className={'doubleInputModalRight'}>
                  <FormItem
                    labelCol={{
                      xs: { span: 24 },
                      sm: { span: 8 }
                    }}
                    wrapperCol={{
                      xs: { span: 24 },
                      sm: { span: 7 }
                    }}
                  >
                    {getFieldDecorator('endTime', {
                      initialValue: modalData.endTime,
                      rules: [
                        {
                          required: true,
                          message: '不能为空!'
                        }
                      ]
                    })(<Input disabled />)}
                  </FormItem>
                </div>
              </div>
            </Col>
          </Row>
          <Row type='flex' justify='center'>
            <Col>
              <div style={{ marginTop: '20px' }}>
                <Button
                  style={{ marginRight: '48px' }}
                  onClick={this.handleCloseModal}
                >
                  取消
                </Button>
                <Button
                  type='primary'
                  htmlType='submit'
                  loading={modalTuttonLoading}
                >
                  保存
                </Button>
              </div>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



