import React, { PureComponent } from 'react'
import { Button, message } from 'antd'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { debounce } from 'lodash'
@connect((state) => ({ ...state.diffApportionmentDet }), mapDispatchToProps)
export default class TableTool extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  // 差异分摊  未完成步骤执行
  handleApplyUnfinisheed = () => {
    const { searchParams, history } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    const { companyCode, period } = reqObj
    this.props.doApplyUnifinished(
      { searchParams: { companyCode, shareDate: period } },
      (data) => {
        if (data && data.result && data.result.success == true) {
          message.success('操作成功,后台数据处理中...', 2, () => {
            history.push('/mms/costCenter/diffApportionment')
          })
        }
      }
    )
  }
  // 查看数据
  handleCheckData = () => {
    const { history, searchParams } = this.props
    console.log(this.props.location, 9999111)
    const { period, companyName, companyCode } = searchParams
    const { pathname, search } = this.props.location || {}
    history.push(
      `/mms/costCenter/diffApportionmentData?locationUrl=${
        encodeURIComponent(pathname + search) || ''
      }&period=${period}&companyCode=${companyCode}&companyName=${
        companyName || ''
      }`
    )
  }
  // 差异过账

  handleAllNotice = debounce(() => {
    const { searchParams, history } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }

    const { companyCode, period } = reqObj
    this.props.handleAllNotice(
      { searchParams: { companyCode, shareDate: period } },
      (data) => {
        if (data && data.result && data.result.success == true) {
          message.success('操作成功,后台数据处理中...', 2, () => {
            history.push('/mms/costCenter/diffApportionment')
          })
        }
      }
    )
  }, 300)
  // 导出
  handleExport = debounce(() => {
    const { handleExport, searchParams, page, size } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    handleExport({ searchParams: { page, size, ...reqObj } }, () => {
      // message.success('导出成功')
    })
  }, 300)
  render() {
    const {
      editOrCreate,
      hasGetSteps,
      btnClickStatus = {},
      flag_apportionmentInTable,
      flag_checkData,
      doDiffLoading,
      flag_postTable
    } = this.props
    const { flagDiffTransfer, flagDiffSend, flagExport = true } = btnClickStatus
    let disabledBool =
      editOrCreate == 'create' && !hasGetSteps // 新建差异分摊数据 并且还没有成功获取了步骤 此时不可以点击差异过账
        ? true
        : false
    console.log('2222', disabledBool, flagExport)
    return (
      <div className={style['table-tool']}>
        {flag_apportionmentInTable && (
          <Button
            type="primary"
            className={style.btn}
            disabled={disabledBool || flagDiffTransfer}
            style={{ marginRight: '10px' }}
            loading={doDiffLoading}
            onClick={this.handleApplyUnfinisheed}
          >
            差异分摊
          </Button>
        )}
        {/* {showOrNotflagUnfinished ? (
          <Button
            type='primary'
            className={style.btn}
            disabled={disabledBool || flagUnfinished}
            style={{ marginRight: '10px' }}
            onClick={this.handleApplyUnfinisheed}
          >
            未完成步骤执行
          </Button>
        ) : null} */}

        {flag_checkData && (
          <Button
            type="primary"
            className={style.btn}
            // disabled={editOrCreate == 'edit' ? false : true}
            disabled={disabledBool}
            style={{ marginRight: '10px' }}
            onClick={this.handleCheckData}
          >
            查看数据
          </Button>
        )}
        {flag_postTable && (
          <Button
            type="primary"
            className={`${(style.btn, style.btn_right)}`}
            disabled={disabledBool || flagDiffSend}
            onClick={this.handleAllNotice}
          >
            差异过账
          </Button>
        )}
        {
          <Button
            type="primary"
            className={`${style.btn}`}
            disabled={disabledBool || flagExport}
            onClick={this.handleExport}
          >
            分摊明细导出
          </Button>
        }
      </div>
    )
  }
}



