import * as actionType from './actionType'
import { getData } from 'util'
import { diffStepStatus } from 'enum'
import {
  API_DIFFAPPORTIONMENT_DETS_STEPLIST,
  API_DIFFAPPORTIONMENT_DETS_STEPLIST_DET,
  API_DIFFAPPORTIONMENT_DETS_APPLY_UNFINISHED,
  API_DIFFAPPORTIONMENT_DETS_APPLY_ALLNOTICE,
  API_DIFFAPPORTIONMENT_DETS_EXPORT
} from 'api'
//同步
export const update = (object) => {
  console.log('object is ', object)
  return {
    type: actionType.DIFFAPPORTIONMENT_DET_UPDATE,
    data: object
  }
}
/* 月底差异分摊-步骤状态 */
// export const diffStepStatus = {
//   0: { key: '0', value: '未完成' },
//   1: { key: '1', value: '成功' },
//   2: { key: '2', value: '运行中' },
//   9: { key: '9', value: '失败' }
// }
//步骤数据处理，控制差异分摊, 未完成步骤执行，差异过账的按钮可点击状态
export const updateBtnStatus = (object, dispatch) => {
  let lenOfList = object && object.length
  let result = null
  let flagSucessNumber = 0, // 差异分摊
    flagDiffTransfer = false, // 差异分摊
    flagUnfinished = false, // 未完成步骤执行
    flagDiffSend = true, // 差异过账
    flagExport = true, // 分摊明细导出
    showOrNotflagUnfinished = false // 差异过账
  object &&
    object.length > 0 &&
    object.map((item) => {
      const { status = '' } = item
      if (status == diffStepStatus[2].key) {
        // 存在运行中的状态,则所有按钮不可点击
        flagDiffTransfer = true
        flagUnfinished = true
        flagDiffSend = true
      }
      if (status == diffStepStatus[9].key) {
        // 存在失败的状态
        showOrNotflagUnfinished = true
      }
      if (status == diffStepStatus[1].key) {
        // 所有成功状态的步骤总数
        flagSucessNumber += 1
      }
    })
  // 差异过账(数组结构中的最后一个状态)的状态为未完成或者失败，并且前边的所有的步骤均为成功，则差异过账按钮可点击  status: 1 - 成功
  if (
    object[lenOfList - 1] &&
    (object[lenOfList - 1].status == '0' ||
      object[lenOfList - 1].status == '9') &&
    Number(flagSucessNumber) + 1 == lenOfList
  ) {
    flagDiffSend = false
  }
  // 差异过账成功，则分摊明细导出按钮可点击  status: 1 - 成功
  if (object[lenOfList - 1] && object[lenOfList - 1].status == '1') {
    flagExport = false
  }
  result = {
    flagDiffTransfer,
    flagUnfinished,
    showOrNotflagUnfinished,
    flagExport,
    flagDiffSend
  }
  return dispatch({
    type: actionType.DIFFAPPORTIONMENT_DET_UPDATE,
    data: {
      btnClickStatus: result,
      doDiffLoading: false
    }
  })
}

// 获取步骤列表-获取步骤按钮
export const handleGetList = (reqData, callBack) => (dispatch) => {
  dispatch({
    type: actionType.DIFFAPPORTIONMENT_DET_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_DIFFAPPORTIONMENT_DETS_STEPLIST, ...reqData })
    .then((data) => {
      // 相应的逻辑判断
      let { page, size } = reqData.searchParams
      dispatch({
        type: actionType.DIFFAPPORTIONMENT_DET_UPDATE,
        data: {
          page: page ? page : 1,
          size: size ? size : 5,
          tableLoading: false,
          searchResult: data.result || []
        }
      })
      updateBtnStatus && updateBtnStatus(data.result || [], dispatch)
      callBack && callBack(data)
    })
    .catch((err) => {
      console.error(err)
      dispatch({
        type: actionType.DIFFAPPORTIONMENT_DET_FAIL
      })
    })
}
// 获取步骤列表-点击详情(根据id)
export const handleGetListDet = (reqData, callBack) => (dispatch) => {
  dispatch({
    type: actionType.DIFFAPPORTIONMENT_DET_UPDATE,
    data: { tableLoading: true }
  })
  getData({ api: API_DIFFAPPORTIONMENT_DETS_STEPLIST_DET, ...reqData })
    .then((data) => {
      // 相应的逻辑判断
      let { page, size } = reqData.searchParams
      updateBtnStatus && updateBtnStatus(data.result || [], dispatch)
      callBack && callBack(data)
      dispatch({
        type: actionType.DIFFAPPORTIONMENT_DET_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          searchResult: data.result || []
        }
      })
    })
    .catch((err) => {
      console.error(err)
      dispatch({
        type: actionType.DIFFAPPORTIONMENT_DET_FAIL
      })
    })
}
